import React from 'react'
import { useNavigation } from '@react-navigation/native';
import { app, db } from '../../Core/Config'
import { collection, getDocs, query, where, orderBy, onSnapshot, limit, addDoc } from 'firebase/firestore';
import { getAuth, signOut } from "firebase/auth";
import { async } from '@firebase/util';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Alert } from 'react-native';

// =========== 登出 ===========
export const handleSignOut = (props) => {
  const auth = getAuth(app);
  const navigation = useNavigation()
  signOut(auth)
    .then(() => {
      navigation.replace('LoginPage')
    })
    .catch(err => alert(err.message))
}
// =========== 登出 ===========


// =========== 刷新資料 ===========
export const data_ReFlash = async () => {
  // 篩遠特定資料
  const q = query(collection(db, "希恩之家"), where("name", "!=", ""));
  let list = []
  const querySnapshot = await getDocs(q);
  querySnapshot.forEach((doc) => {
    // console.log(doc.id, " => ", doc.data());
    let arr = doc.data()
    arr['id'] = doc.id
    list.push(arr)
  });

  // group姓名
  let result = Object.entries(list.reduce((acc, { name }) => {
    acc[name] = (acc[name] || []);
    acc[name].push({ name });
    return acc;
  }, {})).map(([key]) => ({ name: key }));

  // 再新增資料
  const color = ['#F7DBCD', '#C7DEE2', '#F0D6A8', '#B4C5E1']
  const color2 = ['#F5C3A2', '#99C4CE', '#E7B358', '#88A0C6']
  let total = []
  for (let i = 0; i < result.length; i++) {
    const q = query(collection(db, "希恩之家"), where("name", "==", result[i].name), orderBy("date", 'desc'), limit(1));
    const querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc) => {
      let aoc = doc.data()
      let pushData = result[i]         // 把 result 每筆拆開
      pushData['color'] = color[i % color.length]    // 新增 color 欄位
      pushData['color2'] = color2[i % color.length]    // 新增 color 欄位
      pushData['date'] = aoc.date   // 新增日期
      pushData['babyId'] = aoc.babyId   // 新增寶寶ID
      total.push(pushData)   // 再將拆開的 result 跟 color 打包成一筆放到 total 內
    });
  }
  console.log(total)
  return total
}
// =========== 刷新資料 ===========

// 新增一筆資料
export const data_Add = async (props) => {
  // 設定從AddNurseTime傳過來的值
  const passProps = JSON.parse(props)
  // console.log(passProps.name)
  try {
    const docRef = await addDoc(collection(db, "希恩之家"), {
      babyId: passProps.BabyId,
      name: passProps.name,
      nurse: passProps.nurse,
      date: passProps.date,
      empNo: passProps.empNo,
      empName: passProps.empName
    });
    console.log('新增紀錄ID:', docRef.id);
    Alert.alert('訊息通知','新增成功')
  } catch (err) {
    console.error('記錄新增錯誤', err);
    Alert.alert('錯誤訊息','新增失敗')
  }
}

// ====== 讀取登入資料 ======
export const Read_Data = async (props) => {
  const q = query(collection(db, "users"), where("empEmail", "==", props));
  let data = ''
  const querySnapshot = await getDocs(q);
  querySnapshot.forEach((doc) => {
    // doc.data() is never undefined for query doc snapshots
    console.log(doc.id, " => ", doc.data());
    data = doc.data()
  });

  // ====== 將註冊資訊存儲到AsyncStorage ======
  const storeData = async () => {
    try {
      let jsonStr = data
      const jsonValue = JSON.stringify(jsonStr)
      await AsyncStorage.setItem('userData', jsonValue)
    } catch (err) {
      console.log('AsyncStorage儲存錯誤', err)
    }
  }
  storeData()
}

