import { StyleSheet, Text, View, Button, TouchableOpacity } from 'react-native'
import React, { useState } from 'react'
import DateTimePicker from '@react-native-community/datetimepicker';
import { useNavigation } from '@react-navigation/native';
import { DateTimePickerAndroid } from '@react-native-community/datetimepicker';


// 完整日期&時間
export const FullTime = (props) => {
  // 調整時間成AMPM格式
  let date = props.substr(0, 10)
  let hours = props.slice(11, 13)
  let minutes = props.slice(14, 16)
  let ampm = hours >= 12 ? '下午' : '上午';
  hours = hours % 12;
  hours = hours ? hours : 12;
  hours = hours.toString().padStart(2, '0');   //補滿2位數
  minutes = minutes.toString().padStart(2, '0');   //補滿2位數
  let strTime = date + ' ' + ampm + hours + ':' + minutes;
  return strTime
}

// 時間轉換上/下午
export const TimeAMPM = (props) => {
  // 調整時間成AMPM格式
  let date = props.substr(0, 10)
  let hours = props.slice(11, 13)
  let minutes = props.slice(14, 16)
  let ampm = hours >= 12 ? '下午' : '上午';
  hours = hours % 12;
  hours = hours ? hours : 12;
  hours = hours.toString().padStart(2, '0');   //補滿2位數
  minutes = minutes.toString().padStart(2, '0');   //補滿2位數
  let strTime = ampm + ' ' + hours + ':' + minutes;
  return strTime
}

// 讀取現在時間
export const NowTime = (props) => {
  let tempDate = new Date();
  // 設定日期
  let year = tempDate.getFullYear()
  let month = (tempDate.getMonth() + 1).toString().padStart(2, '0')
  let day = tempDate.getDate().toString().padStart(2, '0')
  let ndate = year + '/' + month + '/' + day
  // 設定時間
  let hours = tempDate.getHours()
  let minutes = tempDate.getMinutes()
  hours = hours.toString().padStart(2, '0');   //補滿2位數
  minutes = minutes.toString().padStart(2, '0');   //補滿2位數
  let strTime = ndate + ' ' + hours + ':' + minutes;
  return strTime
}



// 日期時間選擇器緩換成可以讀取的格式
export const TimePickerState = (props) => {
  let year = props.getFullYear()
  let month = (props.getMonth() + 1).toString().padStart(2, '0')
  let date = props.getDate().toString().padStart(2, '0')
  let fDate = year + '/' + month + '/' + date
  let hours = props.getHours()
  let minutes = props.getMinutes()
  hours = hours.toString().padStart(2, '0')
  minutes = minutes.toString().padStart(2, '0');
  let fTime = hours + ':' + minutes;
  return fDate + ' ' + fTime
}

