import { Alert, StyleSheet, Text, TextInput, View, TouchableWithoutFeedback, Keyboard, KeyboardAvoidingView, TouchableOpacity, Platform } from 'react-native'
import React, { useState } from 'react'
import { useNavigation } from '@react-navigation/native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import { app, db } from '../../Core/Config'
import { setDoc, doc } from "firebase/firestore";

const SignupScreen = (props) => {

  const [userName, setUserName] = useState('')
  const [userId, setUserId] = useState('')
  const [userEmail, setUserEmail] = useState('')
  const [userPwd, setUserPwd] = useState('')
  const [userPwdDChk, setUserPwdDChk] = useState('')
  const [userPwdState, setUserPwdState] = useState('')  // 密碼格式判斷
  const [seeChkText, setSeeChkText] = useState('none')
  const [seePwd, setSeePwd] = useState(true)    // TODO是否顯示密碼
  const auth = getAuth(app);

  // 註冊帳號
  const handleSignUp = () => {
    if (userPwdDChk === userPwd) {
      createUserWithEmailAndPassword(auth, userEmail, userPwd)
        .then(async userCredentials => {
          const user = userCredentials.user;

          // ====== 將註冊資訊存儲到AsyncStorage ======
          const storeData = async () => {
            try {
              let jsonStr = { empNo: userId, empName: userName, empEmail: userEmail }
              const jsonValue = JSON.stringify(jsonStr)
              await AsyncStorage.setItem('userData', jsonValue)
            } catch (err) {
              console.log('註冊AS錯誤', err)
            }
          }
          storeData()
          // ====== 將註冊資訊存儲到AsyncStorage ======

          await setDoc(doc(db, "users", user.uid), {      //同時寫入使用者個人資料
            empName: userName,
            empNo: userId,
            empEmail: userEmail,
          })
          Alert.alert('註冊訊息', '註冊成功')
        })
        .catch((err) => {
          if (err.message === 'Firebase: Error (auth/invalid-email).' || 'Firebase: Error (auth/wrong-password).') {
            Alert.alert('錯誤訊息', '帳號或密碼錯誤')
          } else {
            Alert.alert('錯誤訊息', err.message)
          }
          console.log(err.message)
        })
    } else {
      Alert.alert('錯誤訊息', '密碼不一致')
    }
  }


  // ====== 密碼格式判斷 ======
  const isValidUserId = (value) => {
    const pattern = /^.{6,16}$/;
    if (!pattern.test(value)) {
      setSeeChkText('flex')
      return '密碼長度為6-16個字'
    }

    const isNonWhiteSpace = /^\S*$/;
    if (!isNonWhiteSpace.test(value)) {
      setSeeChkText('flex')
      return '密碼中不包含空格'
    }
    setSeeChkText('none')
    return true
  }

  // 選取到密碼輸入時判斷身份證的格式是否正確
  const pwdCheck = () => {
    const checkPwd = isValidUserId(userPwd)
    if (checkPwd) {
      setUserPwdState(checkPwd)
      console.log(checkPwd)
    }
  }

  // ====== 判斷裝置是否要收起鍵盤 ======
  const kbCheck = () => {
    let board = ''
    if (Platform.OS === 'ios') {
      board = Keyboard.dismiss
    } else if (Platform.OS === 'android') {
      board = Keyboard.dismiss
    } else {
    }
    return board
  }

  return (
    <TouchableWithoutFeedback onPress={kbCheck()}>
      <KeyboardAvoidingView style={{ flex: 1 }} behavior={Platform.OS === "ios" ? "padding" : "height"}>
        <View style={styles.container}>
          <View style={styles.main_bg}>
            <View style={styles.input_area}>
              <Text style={styles.input_title}>名稱</Text>
              <TextInput
                style={styles.input_text}
                placeholder='王小明'
                placeholderTextColor={'#ADADAD'}
                value={userName}
                onChangeText={(text) => setUserName(text)} />
            </View>
            <View style={styles.input_area}>
              <Text style={styles.input_title}>編號</Text>
              <TextInput
                style={styles.input_text}
                placeholder='A01234'
                placeholderTextColor={'#ADADAD'}
                value={userId}
                onChangeText={(text) => setUserId(text)} />
            </View>
            <View style={styles.input_area}>
              <Text style={styles.input_title}>電子信箱</Text>
              <TextInput
                style={styles.input_text}
                placeholder='someone@abc.com'
                placeholderTextColor={'#ADADAD'}
                value={userEmail}
                onChangeText={(text) => setUserEmail(text)} />
            </View>
            <View style={styles.input_area}>
              <Text style={styles.input_title}>密碼</Text>
              <TextInput
                style={styles.input_text}
                placeholder='6~16位數'
                placeholderTextColor={'#ADADAD'}
                value={userPwd}
                secureTextEntry
                onFocus={() => setUserPwdState('')}
                onChangeText={(text) => setUserPwd(text)} />
              <Text style={[styles.userPwdState_text, { display: seeChkText }]}>{userPwdState}</Text>
            </View>
            <View style={styles.input_area}>
              <Text style={styles.input_title}>確認密碼</Text>
              <TextInput
                style={styles.input_text}
                placeholder='6~16位數'
                placeholderTextColor={'#ADADAD'}
                value={userPwdDChk}
                secureTextEntry
                onFocus={() => pwdCheck()}
                onChangeText={(text) => setUserPwdDChk(text)} />
            </View>

            <View style={{ height: '10%', flexDirection: 'row', marginTop: 30 }}>
              <TouchableOpacity style={styles.back_btn} onPress={() => props.navigation.pop()}>
                <Text style={styles.btn_text}>返回</Text>
              </TouchableOpacity>
              <TouchableOpacity style={styles.reg_btn} onPress={() => handleSignUp()}>
                <Text style={styles.btn_text}>註冊</Text>
              </TouchableOpacity>
            </View>
          </View>
        </View>
      </KeyboardAvoidingView>
    </TouchableWithoutFeedback>
  )
}

export default SignupScreen

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  main_bg: {
    width: '84%',
    backgroundColor: '#68AEA0',
    borderRadius: 30,
    alignItems: 'center',
    paddingVertical: 20,
    paddingHorizontal: 30
  },
  input_area: {
    width: '100%',
    alignItems: 'flex-start',
    marginBottom: 20
  },
  input_title: {
    color: '#3D5478',
    fontWeight: 'bold',
    fontSize: 16,
    marginBottom: 4,
  },
  input_text: {
    width: '100%',
    height: 40,
    backgroundColor: '#EBEBEB',
    borderRadius: 10,
    paddingLeft: 10,
  },
  userPwdState_text: {
    color: '#DB0E3F',
    fontSize: 12,
    fontWeight: 'bold',
    marginTop: 2,
  },
  back_btn: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#EBEBEB',
    borderRadius: 10,
    marginRight: 10
  },
  reg_btn: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#99C4CE',
    borderRadius: 10,
    marginLeft: 10
  },
  btn_text: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#3D5478'
  }
});
