import React from 'react';
import { StyleSheet, Easing } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator, CardStyleInterpolators } from '@react-navigation/stack';
import { ToastProvider } from "react-native-toast-notifications";


// 其他頁面
import LoginScreen from './src/screens/LoginScreen'
import SignupScreen from './src/screens/SignupScreen';
import HomeScreen from './src/screens/HomeScreen';
import HomeDetailScreen from './src/screens/HomeDetailScreen'
import AddNurseTime from './src/screens/AddNurseTime';

const Stack = createStackNavigator();

// Stack開啟換頁時動畫設定
const config = {
  animation: 'spring',
  config: {
    stiffness: 1000,
    damping: 500,
    mass: 3,
    overshootClamping: true,
    restDisplacementThreshold: 0.01,
    restSpeedThreshold: 0.01,
  },
};
// Stack關閉換頁時動畫設定
const closeConfig = {
  animation: 'timing',
  config: {
    duration: 500,
    easing: Easing.linear
  }
}
const popUp_o = {
  headerShown: false,
  presentation: 'transparentModal',
  cardStyleInterpolator: CardStyleInterpolators.forBottomSheetAndroid,    // 由下往上滑出
  cardOverlayEnabled: false,    // 是否背景顯示陰影
  transitionSpec: {
    open: config,
    close: closeConfig
  },
}
const popUp_s = {
  headerShown: false,
  presentation: 'transparentModal',
  cardStyleInterpolator: CardStyleInterpolators.forBottomSheetAndroid,    // 由下往上滑出
  cardOverlayEnabled: true,    // 是否背景顯示陰影
  transitionSpec: {
    open: config,
    close: closeConfig
  },
}

export default function App() {
  return (
    <ToastProvider placement='top' normalColor='rgba(0, 0, 0, 0.25)'>
      <NavigationContainer>
        <Stack.Navigator screenOptions={{ gestureEnabled: true, cardOverlayEnabled: true }}>
          <Stack.Screen name="LoginPage" component={LoginScreen} options={{ headerShown: false }} />
          <Stack.Screen name="SignupPage" component={SignupScreen} options={{ headerShown: false, presentation: 'transparentModal' }} />
          <Stack.Screen name="HomePage" component={HomeScreen} options={{ headerShown: false }} />
          <Stack.Screen name="HomeDetailPage" component={HomeDetailScreen} options={popUp_o} />
          <Stack.Screen name="AddNurseTimePage" component={AddNurseTime} options={popUp_s} />
        </Stack.Navigator>
      </NavigationContainer>
    </ToastProvider>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    // backgroundColor: '#d4d4d4',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
