import { Button, StyleSheet, Text, View, FlatList, TouchableOpacity, TouchableHighlight } from 'react-native'
import React, { useState, useEffect } from 'react'
import { collection, getDocs, query, where, orderBy, onSnapshot, limit } from 'firebase/firestore';
import { getAuth, signOut } from "firebase/auth";
import { app, db } from '../../Core/Config'
import { useNavigation } from '@react-navigation/native';
import { ToastProvider, useToast } from "react-native-toast-notifications";
import AsyncStorage from '@react-native-async-storage/async-storage';
import Ionicons from 'react-native-vector-icons/Ionicons';

import { MainCard } from '../components/Card'
import TextStyle from '../style/TextStyle';
import { handleSignOut } from '../components/DataProcess'
import { FullTime } from '../components/Time';


const HomeScreen = (props) => {
  const auth = getAuth(app);
  const navigation = useNavigation()
  const [userDoc, setUserDoc] = useState([])
  const [pageName, setPageName] = useState('寶寶日誌')
  const [search, setSearch] = useState('')    // TODO 搜尋功能實作
  const toast = useToast();


  useEffect(() => {
    const q = query(collection(db, "希恩之家"), where("name", "!=", search));
    const unsubscribe = onSnapshot(q, async (querySnapshot) => {
      const list = [];
      querySnapshot.forEach((doc) => {
        // console.log(doc.id, " => ", doc.data());
        let arr = doc.data()
        arr['id'] = doc.id
        list.push(arr)
      });

      // group姓名
      const result = Object.entries(list.reduce((acc, { name }) => {
        acc[name] = (acc[name] || []);
        acc[name].push({ name });
        return acc;
      }, {})).map(([key]) => ({ name: key }));

      // 再新增資料
      const color = ['#F7DBCD', '#C7DEE2', '#F0D6A8', '#B4C5E1']
      const color2 = ['#F5C3A2', '#99C4CE', '#E7B358', '#88A0C6']
      let total = []
      for (let i = 0; i < result.length; i++) {
        const q = query(collection(db, "希恩之家"), where("name", "==", result[i].name), orderBy("date", 'desc'), limit(1));
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
          let aoc = doc.data()
          let pushData = result[i]         // 把 result 每筆拆開
          pushData['color'] = color[i % color.length]    // 新增 color 欄位
          pushData['color2'] = color2[i % color.length]    // 新增 color 欄位
          pushData['date'] = aoc.date   // 新增日期
          pushData['babyId'] = aoc.babyId   // 新增寶寶ID
          total.push(pushData)   // 再將拆開的 result 跟 color 打包成一筆放到 total 內
        });
      }
      setUserDoc(total)
      // console.log(userDoc)
    });
    return unsubscribe
  }, []);

  // =========== 登出 ===========
  const handleSignOut = () => {
    signOut(auth)
      .then(() => {
        navigation.replace('LoginPage')
      })
      .catch(err => alert(err.message))
  }
  // =========== 登出 ===========


  // =========== 刷新資料 ===========
  const data_ReFlash = async () => {
    // 篩遠特定資料
    const q = query(collection(db, "希恩之家"), where("name", "!=", ""));
    let list = []
    const querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc) => {
      // console.log(doc.id, " => ", doc.data());
      let arr = doc.data()
      arr['id'] = doc.id
      list.push(arr)
    });

    // group姓名
    let result = Object.entries(list.reduce((acc, { name }) => {
      acc[name] = (acc[name] || []);
      acc[name].push({ name });
      return acc;
    }, {})).map(([key]) => ({ name: key }));

    // 再新增資料
    const color = ['#F7DBCD', '#C7DEE2', '#F0D6A8', '#B4C5E1']
    const color2 = ['#F5C3A2', '#99C4CE', '#E7B358', '#88A0C6']
    let total = []
    for (let i = 0; i < result.length; i++) {
      const q = query(collection(db, "希恩之家"), where("name", "==", result[i].name), orderBy("date", 'desc'), limit(1));
      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        let aoc = doc.data()
        let pushData = result[i]         // 把 result 每筆拆開
        pushData['color'] = color[i % color.length]    // 新增 color 欄位
        pushData['color2'] = color2[i % color.length]    // 新增 color 欄位
        pushData['date'] = aoc.date   // 新增日期
        pushData['babyId'] = aoc.babyId   // 新增寶寶ID
        total.push(pushData)   // 再將拆開的 result 跟 color 打包成一筆放到 total 內
      });
    }
    setUserDoc(total)

    // 顯示讀取中的提示
    let load_toast = toast.show("讀取中...", { duration: 1000, });
    toast.update(load_toast, "Loading completed", { type: "success" });   // 沒有作用
  }
  // =========== 刷新資料 ===========


  const showNoticeDetail = (cases) => {
    props.navigation.push('HomeDetailPage', { passProps: cases })
    console.log(cases)
  }

  const addTime = (addTime) => {
    props.navigation.push('AddNurseTimePage', { passProps: addTime })
    // console.log(addTime)
  }

  const renderBook = (cases) => {
    return (
      <TouchableOpacity>
        <View>
          <MainCard
            babyPic={require('../icon/baby-boy.png')}
            babyId={cases.babyId}
            babyName={cases.name}
            card_color={cases.color}
            nearTime={FullTime(cases.date)}
            cardOnPress={() => showNoticeDetail(cases)}
          />
        </View>
      </TouchableOpacity>
    )
  }

  return (
    <ToastProvider>
      <View style={styles.container}>
        <View style={{ flex: 1, width: '100%', alignItems: 'center' }}>
          <Text style={TextStyle.home_title}>寶寶日誌</Text>
          {/* <Button title='搜尋' onPress={() => toast.show("123")}></Button> */}
          <View style={{ alignItems: 'flex-end', width: '90%' }}>
            <Ionicons name={'search'} size={25} color={'#3D5478'} onPress={() => handleSignOut()} />
          </View>
        </View>
        <View style={styles.FlatView}>
          {/* TODO 新增寶寶 */}
          <FlatList
            data={userDoc}
            renderItem={cases => renderBook(cases.item)}
            keyExtractor={cases => cases.name}
            refreshing={false}
            onRefresh={() => data_ReFlash()}
          />
          <View style={styles.bottom_bar}>
            <View style={{ position: 'absolute', top: -30 }}>
              <TouchableOpacity onPress={() => addTime(userDoc)}>
                <Ionicons name={'add-circle'} size={50} color={'#3D5478'} />
              </TouchableOpacity>
            </View>
          </View>
        </View>

      </View>
    </ToastProvider>
  )
}

export default HomeScreen

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#C9CBE0',
    alignItems: 'center',
  },
  FlatView: {
    width: '93%',
    height: '86%',
    backgroundColor: '#EBEBEB',
    // borderTopLeftRadius: 30,
    // borderTopRightRadius: 30,
    borderRadius: 30,
    alignItems: 'center',
    justifyContent: 'center',
    // paddingBottom:70
  },
  bottom_bar: {
    // backgroundColor: '#DBDDF0',
    width: '100%',
    alignItems: 'center',
    borderTopLeftRadius: 30,
    borderTopRightRadius: 30,
    height: 30
  }
});
