import { Keyboard, StyleSheet, Text, TextInput, TouchableWithoutFeedback, View, Button, TouchableOpacity, Alert, Platform } from 'react-native'
import React, { useEffect, useState } from 'react'
import Ionicons from 'react-native-vector-icons/Ionicons';
import DateTimePicker from '@react-native-community/datetimepicker';
import AsyncStorage from '@react-native-async-storage/async-storage';


import TextStyle from "../style/TextStyle";
import LazDropdown from '../components/DropDown'
import * as Time from '../components/Time.js'
import { data_Add } from '../components/DataProcess'

const AddNurseTime = (props) => {
  // 設定從HomeScreen傳過來的值
  const passProps = props.route.params.passProps || 'noting get'
  // 設定從下拉選擇傳回來的值
  const dropBabyId = props.route.params.BabyId || 'noting get'
  const dropName = props.route.params.name || 'noting get'

  const chsText = props.route.params.chsText || 'noting get'

  const [date, setDate] = useState(new Date())
  const [mode, setMode] = useState('date')
  const [show, setShow] = useState(false)
  const [NowTime, setNowTime] = useState(Time.NowTime())
  const [nurse, setNurse] = useState('')

  const [dropData, setDropData] = useState([])
  const [userData, setUserData] = useState('')

  useEffect(() => {
    let dataArr = []
    for (let i = 0; i < passProps.length; i++) {
      let arr = {}
      arr['label'] = passProps[i].name + '(' + passProps[i].babyId + ')'
      arr['value'] = passProps[i].babyId
      arr['name'] = passProps[i].name
      dataArr.push(arr)
    }
    setDropData(dataArr)
    const getData = async () => {
      try {
        const jsonValue = await AsyncStorage.getItem('userData')
        setUserData(JSON.parse(jsonValue))
        return jsonValue != null ? JSON.parse(jsonValue) : null;
      } catch (e) {
        // error reading value
      }
    }
    getData()
    console.log('讀取使用者', userData.empName)
  }, [])

  // ========== 日期時間選擇器 ==========
  const onChange = (event, selectedDate) => {
    const currentDate = selectedDate || date
    setShow(false)
    setDate(currentDate)
    let time = Time.TimePickerState(new Date(selectedDate))
    setNowTime(time)
  }

  const showMode = (currentMode) => {
    setShow(true)
    setMode(currentMode)
  }
  // ========== 日期時間選擇器 ==========



  // ====== 上傳紀錄資料 ======
  const sendData = () => {
    let dataArr = {
      BabyId: dropBabyId,
      name: dropName,
      nurse: nurse,
      date: NowTime,
      empNo: userData.empNo,
      empName: userData.empName
    }
    if (dataArr.nurse === '' || dataArr.BabyId === 'noting get') {
      Alert.alert('錯誤訊息', '請填寫完整')
    } else {
      console.log(dataArr)
      data_Add(JSON.stringify(dataArr))
    }
  }
  // ====== 上傳紀錄資料 ======

  const kbCheck = () => {
    let board = ''
    if (Platform.OS === 'ios') {
      board = Keyboard.dismiss
    } else if (Platform.OS === 'android') {
      board = Keyboard.dismiss
    } else {
    }
    return board
  }

  return (
    <TouchableWithoutFeedback onPress={kbCheck()}>
      <View style={styles.container}>
        <View style={{ alignItems: 'flex-start', width: '100%', marginLeft: 20, marginBottom: 2 }}>
          <Ionicons name={'chevron-back'} size={36} color={'#EBEBEB'} onPress={() => props.navigation.pop()} />
        </View>
        <View style={styles.main_bg}>
          <View style={styles.record_bg}>
            <View style={{ width: '80%', justifyContent: 'center', alignItems: 'center' }}>
              {/* TODO新增成員頁面 */}
              <Text style={styles.new_member} onPress={() => { console.log('123') }}>新增成員</Text>
              <View style={{ marginVertical: 16 }}>
                <Text style={TextStyle.detail_title}>新增紀錄</Text>
              </View>

              {/* 寶寶選擇 */}
              <View style={[styles.box, { paddingVertical: 10 }]}>
                <View style={{ width: '60%', marginBottom: 16 }}>
                  <LazDropdown data={dropData} />
                </View>
              </View>

              {/* 喝奶量紀錄 */}
              <View style={styles.box}>
                <Text style={[TextStyle.title_16, { marginBottom: 10 }]}>奶量(cc)</Text>
                <TextInput style={styles.input}
                  placeholder={'本次寶寶喝奶量'}
                  placeholderTextColor={'#ADADAD'}
                  maxLength={3}
                  value={nurse}
                  keyboardType={'number-pad'}
                  onChangeText={(text) => setNurse(text)}
                />
              </View>

              {/* 時間紀錄 */}
              <View style={styles.box}>
                <Text style={[TextStyle.title_16, { marginBottom: 4 }]}>紀錄時間</Text>
                <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                  <Text style={[TextStyle.title_16, { marginRight: 10 }]}>{Time.FullTime(NowTime)}</Text>
                  <Ionicons name={'reload-outline'} size={20} color={'#3D5478'} onPress={() => setNowTime(Time.NowTime())} />
                </View>
                {show && (
                  <DateTimePicker
                    testID='dateTimePicker'
                    value={date}
                    mode={mode}
                    is24Hour={false}
                    display='default'
                    onChange={onChange}
                  />)}
                <View style={{ width: '60%' }}>
                  <TouchableOpacity style={styles.button} onPress={() => showMode('date')}>
                    <Text style={styles.btn_text}>選擇日期</Text>
                  </TouchableOpacity>
                </View>
                <View style={{ width: '60%' }}>
                  <TouchableOpacity style={styles.button} onPress={() => showMode('time')}>
                    <Text style={styles.btn_text}>選擇時間</Text>
                  </TouchableOpacity>
                </View>
              </View>

            </View>
          </View>

          <View style={styles.btn_area}>
            <TouchableOpacity style={styles.send_btn} onPress={() => sendData()}>
              <Text style={[styles.btn_text, { fontSize: 18, fontWeight: 'bold', color: '#EBEBEB' }]}>送出</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </TouchableWithoutFeedback>
  )
}

export default AddNurseTime

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  main_bg: {
    width: '93%',
    height: '86%',
    backgroundColor: '#EBEBEB',
    borderRadius: 36,
    marginTop: 10,
    alignItems: 'center',
    marginBottom: 10
  },
  record_bg: {
    width: '100%',
    backgroundColor: '#C9CBE0',
    borderRadius: 36,
    paddingBottom: 10,
    alignItems: 'center'
  },
  box: {
    backgroundColor: '#DBDDF0',
    paddingVertical: 20,
    borderRadius: 20,
    width: '100%',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: 16
  },
  input: {
    width: '60%',
    height: 50,
    backgroundColor: '#EFEFEF',
    borderRadius: 10,
    textAlign: 'center'
  },
  btn_area: {
    alignItems: 'center',
    justifyContent: 'center',
    width: '60%',
    flex: 2
  },
  button: {
    alignItems: 'center',
    borderRadius: 10,
    // borderWidth: 2,
    // borderColor: '#3D5478',
    backgroundColor: '#EFEFEF',
    paddingVertical: 12,
    marginTop: 10
  },
  btn_text: {
    color: '#3D5478',
    fontSize: 16
  },
  send_btn: {
    width: '70%',
    backgroundColor: '#3D5478',
    alignItems: 'center',
    borderRadius: 16,
    paddingVertical: 18,
  },
  new_member: {
    position: 'absolute',
    right: -10,
    top: 22,
    color: '#68AEA0'
  }
});
