import React, { useState, useEffect } from 'react';
import { Button, StyleSheet, Text, View, TouchableOpacity, FlatList, ScrollView } from 'react-native';
import { collection, getDocs, query, where, orderBy, onSnapshot, doc } from 'firebase/firestore';
import { db } from '../../Core/Config'
import { TimeAMPM } from '../components/Time';
import Ionicons from 'react-native-vector-icons/Ionicons';

import { DetailCard } from '../components/Card';
import TextStyle from "../style/TextStyle";
import CardStyle from "../style/CardStyle";


export default function HomeDetailScreen(props) {

  // 設定從HomeScreen傳過來的值
  const passProps = props.route.params.passProps || 'noting get'

  const [timeData, setTimeData] = useState([])
  const [dataSource, setDataSource] = useState([])


  useEffect(() => {
    const q = query(collection(db, "希恩之家"), where("name", "==", passProps.name), orderBy("date", 'desc'));
    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const list = [];
      querySnapshot.forEach((doc) => {
        let arr = doc.data()      // 放入doc.data()的資料
        arr['id'] = doc.id      //新增一筆名稱是"id"的key，將doc.id的值新增進去
        list.push(arr)        // 放到空陣列內
      });
      setDataSource(list)
    });
    return unsubscribe
  }, []);

  // group日期 目前無使用
  // Array.prototype.groupBy = function(prop) {
  //   return this.reduce(function(groups, item) {
  //     const val = item[prop].slice(5,10)
  //     groups[val] = groups[val] || []
  //     groups[val].push(item)
  //     return groups
  //   }, {})
  // };
  // const groupByMake = dataSource.groupBy('date');
  // // console.log('group',groupByMake["06/11"][0].date)

  let nData = []
  for (let i = 0; i < dataSource.length; i++) {
    let dataS = dataSource[i]
    let dataTime = dataSource[i].date.substr(5, 5)
    let lineColor
    if (i % 2 === 0) {
      lineColor = passProps.color2
    } else {
      lineColor = '#f4f4f4'
    }

    nData.push(
      <DetailCard key={i}
        cardStyle={[CardStyle.detail_card_white, { backgroundColor: lineColor }]}
        date={dataS.date.slice(0, 10)}
        clg
        time={TimeAMPM(dataS.date)}
        item={'亞培'}   //TODO
        capacity={dataS.nurse+'cc'}
        name={dataS.empName}
      />
    )
  }

  return (
    <View style={styles.container}>
      <View style={{ alignItems: 'flex-start', width: '100%', marginLeft: 20, marginBottom: 2 }}>
        <Ionicons name={'chevron-back'} size={36} color={'#3D5478'} onPress={() => props.navigation.pop()} />
      </View>
      <View style={styles.main_bg}>
        <View style={[styles.scroll_bg, { backgroundColor: passProps.color }]}>
          <View style={{ marginTop: 16 }}>
            <Text style={TextStyle.detail_title}>{passProps.name}</Text>
          </View>
          <View style={{ height: '100%' }}>
            <ScrollView>
              <View style={{ alignItems: 'center' }}>
                {nData}
              </View>
            </ScrollView>
          </View>
        </View>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  main_bg: {
    width: '93%',
    height: '86%',
    backgroundColor: '#EBEBEB',
    borderTopLeftRadius: 36,
    borderTopRightRadius: 36,
    marginTop: 20,
  },
  scroll_bg: {
    width: '100%',
    flex: 1,
    backgroundColor: '#F7DBCD',
    borderTopLeftRadius: 36,
    borderTopRightRadius: 36,
    marginTop: 20,
    alignItems: 'center'
  }
});
