import React, { useEffect, useState } from 'react'
import { Image, StyleSheet, Text, TextInput, TouchableOpacity, View, KeyboardAvoidingView, Alert, TouchableWithoutFeedback, Keyboard, Platform } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { app } from '../../Core/Config'
import { Read_Data, storeData } from '../components/DataProcess'

export default function LoginScreen(props) {

  const [kbState, setKbState] = useState('')
  // Firebase
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const auth = getAuth(app);
  // 換頁
  const navigation = useNavigation()
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(user => {
      if (user) {
        navigation.replace('HomePage')
      }
    })
    return unsubscribe
  }, [])

  // 帳號登入
  const handleLogin = () => {
    signInWithEmailAndPassword(auth, email, password)
      .then(userCredentials => {
        const user = userCredentials.user;
        Read_Data(user.email)
        // console.log(userData)
        console.log('Logged in with', user.email)
      })
      .catch((err) => {
        if (err.message === 'Firebase: Error (auth/invalid-email).' || 'Firebase: Error (auth/wrong-password).') {
          Alert.alert('錯誤訊息', '帳號或密碼錯誤')
        } else {
          Alert.alert('錯誤訊息', err.message)
        }
        console.log(err.message)
      })
  }

  const kbCheck = () => {
    let board = ''
    if (Platform.OS === 'ios') {
      board = Keyboard.dismiss
    } else if (Platform.OS === 'android') {
      board = Keyboard.dismiss
    } else {
    }
    return board
  }

  return (
    <TouchableWithoutFeedback onPress={kbCheck()}>
      <KeyboardAvoidingView style={styles.container} behavior={Platform.OS === "ios" ? "padding" : "height"}>
        <View style={styles.top_bg}>
        </View>
        <View style={styles.login_bg}>
          <View style={{ width: '100%', alignItems: 'center', marginBottom: 60 }}>
            <Text style={styles.login_title}>LOGIN</Text>
            <View style={styles.login_area}>
              <TextInput
                style={styles.login_input}
                placeholder='電子信箱'
                placeholderTextColor={'#ADADAD'}
                value={email}
                onChangeText={text => setEmail(text)}
              />
              <TextInput
                style={styles.login_input}
                placeholder='密碼'
                placeholderTextColor={'#ADADAD'}
                value={password}
                secureTextEntry
                onChangeText={text => setPassword(text)}
              />
              <Text style={styles.h4_text}>還沒有帳號嗎?
                <Text style={{ color: '#3D5478', fontWeight: 'bold' }}
                  onPress={() => navigation.navigate('SignupPage')}>馬上註冊</Text>
              </Text>
            </View>
          </View>
        </View>

        <TouchableOpacity style={styles.login_icon_bg} onPress={() => handleLogin()}>
          <Image source={require('../icon/right-arrow_y.png')} style={styles.login_icon} />
        </TouchableOpacity>

      </KeyboardAvoidingView>
    </TouchableWithoutFeedback>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#F0D6A8',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 5
  },
  top_bg: {
    backgroundColor: '#E7B358',
    width: '100%',
    flex: 3,
    borderBottomLeftRadius: 60,
  },
  login_bg: {
    flex: 3,
    backgroundColor: '#F0D6A8',
    width: '100%',
    alignItems: "center",
    justifyContent: 'center',
  },
  login_title: {
    color: '#3D5478',
    fontSize: 28,
    fontWeight: 'bold',
    alignSelf: 'flex-start',
    transform: [{ translateX: 40 }]
  },
  login_area: {
    width: '60%',
    alignItems: 'center',
    marginRight: 80
  },
  login_input: {
    width: '90%',
    height: 40,
    backgroundColor: '#EBEBEB',
    borderRadius: 10,
    paddingLeft: 14,
    marginTop: 20,
  },
  login_icon: {
    height: '50%',
    width: '50%',
  },
  login_icon_bg: {
    height: 80,
    width: 80,
    borderRadius: 60,
    backgroundColor: '#3D5478',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'absolute',
    right: 32,
    bottom: 60
  },
  h4_text: {
    fontSize: 12,
    marginTop: 10,
  }
});
