import React, { useEffect, useState } from 'react';
import { StyleSheet, Text, View } from 'react-native';
import { Dropdown } from 'react-native-element-dropdown';
import { useNavigation } from '@react-navigation/native';

const data = [
  { label: 'Item 1', value: '1' },
  { label: 'Item 2', value: '2' },
  { label: 'Item 3', value: '3' },
  { label: 'Item 4', value: '4' },
  { label: 'Item 5', value: '5' },
  { label: 'Item 6', value: '6' },
  { label: 'Item 7', value: '7' },
  { label: 'Item 8', value: '8' },
];

const DropdownComponent = (props) => {

  const [value, setValue] = useState(null);
  const [name, setName] = useState(null);
  const [isFocus, setIsFocus] = useState(false);
  const navigation = useNavigation()


  const renderLabel = () => {
    if (value || isFocus) {
      return (
        <Text style={[styles.label, isFocus && { color: '#68AEA0' }]}>
          要記錄的寶寶
        </Text>
      );
    }
    return null;
  };

  useEffect(() => {
    navigation.navigate('AddNurseTimePage', { BabyId: value, name: name })
    // console.log(name)
  }, [value])


  return (
    <View style={styles.container}>
      {renderLabel()}
      <Dropdown
        style={[styles.dropdown, isFocus && { borderColor: '#68AEA0' }]}
        containerStyle={{ backgroundColor: '#DBDDF0' }}    // 下拉框的樣式
        placeholderStyle={styles.placeholderStyle}    // 選擇前文字框文字的樣式
        selectedTextStyle={styles.selectedTextStyle}    // 選擇後文字框文字的樣式
        itemContainerStyle={[styles.itemContainerStyle, isFocus && { borderColor: '#B97687' }]}    // 下拉框選項的樣式
        itemTextStyle={styles.itemTextStyle}    // 下拉框選項文字樣式
        inputSearchStyle={styles.inputSearchStyle}    // 搜尋文字框文字的樣式
        data={props.data}
        search
        maxHeight={300}
        labelField="label"
        valueField="value"
        placeholder={!isFocus ? '選擇寶寶' : '...'}
        searchPlaceholder="搜尋..."
        value={value}
        onFocus={() => setIsFocus(true)}
        onBlur={() => setIsFocus(false)}
        onChange={item => {
          setValue(item.value);
          setName(item.name)
          setIsFocus(false);
        }}
      />
    </View>
  );
};

export default DropdownComponent;

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#DBDDF0',
    // padding: 16,
    paddingTop: 16
  },
  dropdown: {
    height: 50,
    borderColor: '#3D5478',
    borderWidth: 0.5,
    borderRadius: 8,
    paddingHorizontal: 8,
  },
  icon: {
    marginRight: 5,
  },
  label: {
    position: 'absolute',
    backgroundColor: '#DBDDF0',
    left: 22,
    top: 8,
    zIndex: 999,
    paddingHorizontal: 8,
    fontSize: 14,
    fontWeight: 'bold',
    color: '#3D5478'
  },
  placeholderStyle: {
    fontSize: 16,
    color: '#3D5478',
    textAlign: 'center'
  },
  selectedTextStyle: {
    fontSize: 16,
    color: '#3D5478',
    textAlign: 'center'
  },
  inputSearchStyle: {
    height: 40,
    fontSize: 16,
    backgroundColor: '#DBDDF0',
    color: '#3D5478',
  },
  itemTextStyle: {
    color: '#3D5478',
    textAlign: 'center'
  },
  itemContainerStyle: {
    backgroundColor: '#DBDDF0',
    borderRadius: 10
  }
});